export default {
	props: {
		SexList: {
			type: Array,
			default() {
				return []
			}
		},
		arrayIndex: {
			type: Number,
			default: 0
		},
		state: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		select_icon() {
			this.$emit('select_icon')
		},
		click_event() {
			this.$emit('click_event')
		},
		selectSex(e) {
			this.$emit('selectSex',e.detail.value)
		}
	}
}
